<template>
  <div v-if="datas">
    <div class="pdf" v-show="fileType === 'pdf'">
      <p><b>图书名称:</b> {{datas.name}}</p>
      <p><b>文件大小:</b> {{datas.size}}M</p>
      <p class="arrow">
         <b>相关操作:</b> 
        <span  @click="changePdfPage(0)" class="turn" :class="{grey: currentPage==1}">上一页</span>


        {{currentPage}} / {{pageCount}}
        <span  @click="changePdfPage(1)" class="turn"
          :class="{grey: currentPage==pageCount}" >下一页</span>
     <van-button icon="https://img.yzcdn.cn/vant/logo.png" @click="largeDown"  class="down" size="small" >下载</van-button>
          
       
       
      </p>
        
      <pdf
        :src="datas.small_url"
        :page="currentPage"
        @num-pages="pageCount=$event"
        @page-loaded="currentPage=$event"
        @loaded="loadPdfHandler"
      ></pdf>
    </div>

    <van-overlay :show="show" @click="show = false">
  <div class="wrapper" @click.stop>
       <van-loading color="#fff" class="loading" size="24px">  正传下载...</van-loading>
 
       
  </div>
</van-overlay>
  </div>
</template>

<script>
import pdf from "vue-pdf";
export default {
  data() {
    return {
      id: null,
      datas: null,
      currentPage: 0, // pdf文件页码
      pageCount: 0, // pdf文件总页数
      fileType: "pdf", // 文件类型
      show: false // pdf文件地址
    };
  },
  components: {
    pdf
  },
  created() {
 
   
    this.id = this.$route.query.id;
    this.queryBookInfos(this.id);
  },

  methods: {
    async queryBookInfos(id) {
      let data = await this.API.getBookInfos({ id });
      
      this.datas = data.data;
     
    },

    changePdfPage(val) {
      // console.log(val)
      if (val === 0 && this.currentPage > 1) {
        this.currentPage--;
        // console.log(this.currentPage)
      }
      if (val === 1 && this.currentPage < this.pageCount) {
        this.currentPage++;
        // console.log(this.currentPage)
      }
    },

    // pdf加载时
    loadPdfHandler() {
      this.currentPage = 1; // 加载的时候先加载第一页
    },

    //下载
    largeDown(){
        this.show = true;
        let url = this.datas.large_url;

        this.axios.get(url, {
          responseType: 'blob', //重要
        }).then(response => {

            window.console.log(response)
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement('a');
          let fname = 'report.pdf';
          link.href = url;
          link.setAttribute('download', fname);
          document.body.appendChild(link);
          link.click();
          this.show = false;
        }).catch((err)=>{
          this.$notify(err);
          this.show = false;
        
        })
      
   
    }
  }
};
</script>


